@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
body {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 129%;
  color: #000000;
  background-color: #ffffff;
  margin: 0 auto;
  min-width: 320px;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

html {
  height: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset 100%;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.error.form__label .form__input {
  outline: 2px solid #ff8282;
}

.error.form__label--email::after {
  background-color: #ff8282;
}

.error.form__label--phone::after {
  background-color: #ff8282;
}

.header {
  position: relative;
  line-height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  margin: 0 auto;
  width: 100vw;
  max-width: 1220px;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    gap: 92px;
    max-width: 900px;
    padding-top: 25px;
  }
}
@media (min-width: 1440px) {
  .header {
    padding: 54px 110px 14px 110px;
    align-items: flex-start;
    max-width: 1220px;
    gap: 263px;
  }
}

.header__wrapper {
  display: flex;
  grid-column: 1/3;
  justify-content: space-between;
  align-items: center;
  padding: 12px 4px 12px 20px;
}
@media (min-width: 768px) {
  .header__wrapper {
    padding: 0;
    margin-left: 31px;
    flex-shrink: 0;
  }
}
@media (min-width: 1440px) {
  .header__wrapper {
    margin-left: 0;
    width: 39%;
  }
}

.main-nav {
  width: 100%;
  text-align: center;
}
@media (min-width: 768px) {
  .main-nav {
    width: auto;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    width: 100%;
  }
}

.main-nav__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  padding-top: 22px;
  padding-bottom: 24px;
}
@media (max-width: 767px) {
  .main-nav__link {
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 18px;
    line-height: 24px;
    padding-top: 13px;
    padding-bottom: 8px;
    font-weight: 400;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    padding-top: 16px;
    padding-bottom: 6px;
    font-size: 20px;
    line-height: 30px;
  }
}

@media (max-width: 767px) {
  .main-nav__link:first-child {
    border-top: 1px solid #e6e6e6;
  }
}

@media (min-width: 1440px) {
  .main-nav__link--index {
    color: #ffffff;
  }
}

@media (min-width: 1440px) {
  .main-nav__link--current.main-nav__link--index {
    border-bottom: 2px solid #ffffff;
  }
}

@media (min-width: 768px) {
  .main-nav__link--current {
    border-bottom: 2px solid #68b738;
  }
}

.main-nav__link:hover,
.main-nav__link:focus {
  opacity: 0.6;
}

.main-nav__link:active {
  opacity: 0.3;
}

.main-nav__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  z-index: 10;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    flex-direction: row;
    gap: 33px;
    justify-content: flex-start;
  }
}

.main-nav__toggle {
  position: absolute;
  cursor: pointer;
  content: "";
  top: 21px;
  right: 17px;
  width: 30px;
  height: 20px;
  border: none;
  background-color: #ffffff;
  padding: 0;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__toggle:hover,
.main-nav__toggle:focus {
  opacity: 0.5;
}

.main-nav__toggle:active {
  opacity: 0.2;
}

.main-nav--closed .main-nav__toggle {
  background-image: url("../img/stack.svg#burger-menu");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 24px 18px;
}

.main-nav--opened .main-nav__toggle {
  background-image: url("../img/stack.svg#close");
  background-repeat: no-repeat;
  background-position: center;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }

  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    background-color: #ffffff;
    width: 100%;
    z-index: 7;
    top: 64px;
    right: 0;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__wrapper {
    position: static;
  }

  .main-nav {
    grid-row: 2/3;
    grid-column: 1/4;
  }
}
.hero {
  position: relative;
  color: #ffffff;
  background-image: url("../img/cat-mobile.jpg");
  background-size: 320px 302px;
  background-repeat: no-repeat;
  background-position: top;
  text-align: center;
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: 0 auto;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/cat-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .hero {
    padding-top: 30px;
    padding-left: 57px;
    padding-right: 75px;
    padding-bottom: 14px;
    background-image: none;
    text-align: start;
    min-height: 810px;
    display: block;
    max-width: 900px;
  }
}
@media (min-width: 1440px) {
  .hero {
    padding-left: 107px;
    padding-bottom: 250px;
    padding-top: 71px;
    min-height: auto;
    max-width: 1220px;
  }
}

.hero__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  margin-top: 17px;
  margin-bottom: 20px;
  z-index: 5;
}
@media (min-width: 768px) {
  .hero__title {
    font-size: 60px;
    line-height: 60px;
    color: #000000;
    margin-top: 30px;
    margin-bottom: 43px;
    margin-left: 63px;
  }
}
@media (min-width: 1440px) {
  .hero__title {
    width: 40%;
    margin-top: 22px;
  }
}

.hero__slogan {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 5px;
  margin-bottom: 302px;
  z-index: 4;
}
@media (min-width: 768px) {
  .hero__slogan {
    font-size: 20px;
    line-height: 20px;
    color: #000000;
    margin-bottom: 67px;
    margin-left: 63px;
  }
}
@media (min-width: 1440px) {
  .hero__slogan {
    width: 40%;
    margin-bottom: 40px;
  }
}

.hero__image {
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  top: 167px;
}
@media (min-width: 768px) {
  .hero__image {
    left: 0;
    top: 260px;
    z-index: -1;
  }
}
@media (min-width: 1440px) {
  .hero__image {
    left: 228px;
    top: 12px;
  }
}

.hero::after {
  position: absolute;
  content: "";
  width: 200%;
  background-color: #62a53b;
  height: 302px;
  right: 0;
  left: -50%;
  top: 0;
  z-index: -1;
}
@media (min-width: 768px) {
  .hero::after {
    display: none;
  }
}

.program-card {
  position: relative;
  background-color: #f2f2f2;
  padding: 15px 20px 18px 20px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .program-card {
    padding: 62px 62px 57px 52px;
  }
}
@media (min-width: 1440px) {
  .program-card {
    max-width: 570px;
  }
}

.program-card--slim::before {
  position: absolute;
  content: "";
  background-image: url("../img/stack.svg#cat-sitting");
  background-repeat: no-repeat;
  background-size: 36px 50px;
  width: 50px;
  height: 53px;
  left: 29px;
  top: 20px;
  z-index: 2;
}
@media (min-width: 768px) {
  .program-card--slim::before {
    width: 140px;
    height: 194px;
    background-size: contain;
    left: auto;
    right: 91px;
    top: 82px;
  }
}
@media (min-width: 1440px) {
  .program-card--slim::before {
    width: 70px;
    height: 97px;
    left: 66px;
    top: 42px;
  }
}

.program-card--pro::before {
  position: absolute;
  content: "";
  background-image: url("../img/stack.svg#cat-walking");
  background-repeat: no-repeat;
  background-size: 67px 28px;
  width: 67px;
  height: 50px;
  left: 13px;
  top: 34px;
  z-index: 2;
}
@media (min-width: 768px) {
  .program-card--pro::before {
    width: 268px;
    height: 112px;
    background-size: contain;
    left: auto;
    right: 27px;
    top: 134px;
  }
}
@media (min-width: 1440px) {
  .program-card--pro::before {
    width: 134px;
    height: 56px;
    left: 32px;
    right: 0;
    top: 68px;
  }
}

.program-card::after {
  position: absolute;
  content: "";
  background-color: #68b738;
  width: 50px;
  height: 50px;
  top: 23px;
  left: 22px;
  z-index: 1;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .program-card::after {
    width: 200px;
    height: 200px;
    left: auto;
    right: 62px;
    top: 94px;
  }
}
@media (min-width: 1440px) {
  .program-card::after {
    width: 100px;
    height: 100px;
    left: 52px;
    top: 48px;
  }
}

.program-card__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
  margin-top: 15px;
  margin-bottom: 20px;
  padding-left: 71px;
}
@media (min-width: 768px) {
  .program-card__title {
    padding-left: 0;
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 18px;
    width: 57%;
  }
}
@media (min-width: 1440px) {
  .program-card__title {
    padding-left: 162px;
    margin-bottom: 70px;
  }
}

.program-card__description {
  color: #444444;
  padding-top: 9px;
  padding-bottom: 22px;
  margin-bottom: 19px;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .program-card__description {
    width: 57%;
    font-size: 16px;
    line-height: 24px;
    padding-top: 6px;
    border-bottom: none;
    margin-bottom: 29px;
  }
}
@media (min-width: 1440px) {
  .program-card__description {
    width: auto;
    padding-bottom: 0;
    margin-bottom: 27px;
    padding-top: 4px;
  }
}

.program-card__link {
  position: relative;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
  margin-top: 8px;
  margin-bottom: 18px;
  margin-right: 60px;
}
@media (min-width: 768px) {
  .program-card__link {
    font-size: 20px;
    line-height: 30px;
  }
}
.program-card__link::after {
  position: absolute;
  content: "";
  mask: url("../img/stack.svg#arrow");
  background-color: #000000;
  width: 24px;
  height: 12px;
  bottom: 6px;
  right: -40px;
}
@media (min-width: 768px) {
  .program-card__link::after {
    top: 8px;
    right: -50px;
