}

.programs {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 10px 20px;
  max-width: 400px;
  margin: 0 auto;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .programs {
    gap: 30px;
    padding-left: 31px;
    padding-right: 30px;
    padding-top: 18px;
    max-width: 900px;
  }
}
@media (min-width: 1440px) {
  .programs {
    flex-direction: row;
    justify-content: space-between;
    padding-left: 110px;
    padding-right: 110px;
    max-width: 1220px;
  }
}

.advantages {
  position: relative;
  padding: 10px 21px 9px;
  max-width: 400px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .advantages {
    padding-left: 31px;
    padding-right: 30px;
    padding-bottom: 18px;
    max-width: 900px;
  }
}
@media (min-width: 1440px) {
  .advantages {
    padding-left: 110px;
    padding-right: 106px;
    padding-top: 38px;
    max-width: 1220px;
  }
}

@media (min-width: 768px) {
  .advantages::after {
    position: absolute;
    content: "";
    width: 100vw;
    bottom: 0;
    left: calc((100% - 100vw) / 2);
    height: 1px;
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (min-width: 1440px) {
  .advantages::after {
    display: none;
  }
}

.advantages__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  margin-top: 0;
  margin-bottom: 42px;
}
@media (min-width: 768px) {
  .advantages__title {
    font-size: 60px;
    line-height: 100%;
    margin-top: 24px;
    margin-bottom: 44px;
  }
}

.advantages__item {
  position: relative;
  color: #444444;
  padding-left: 80px;
  margin-bottom: 26px;
  counter-increment: item 1;
}
@media (min-width: 768px) {
  .advantages__item {
    max-width: 250px;
    padding-top: 142px;
    padding-bottom: 57px;
    padding-left: 0;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .advantages__item {
    padding-top: 139px;
  }
}

.advantages__item::before {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  left: 0;
  top: -2px;
}
@media (min-width: 768px) {
  .advantages__item::before {
    width: 80px;
    height: 80px;
    left: 0;
    top: 30px;
  }
}

.advantages__item--healthy::before {
  background: url("../img/stack.svg#leaf"), #68b738;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 25px 28px;
}

.advantages__item--powder::before {
  background: url("../img/stack.svg#glass"), #68b738;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 26px 29px;
}

.advantages__item--meal::before {
  background: url("../img/stack.svg#tableware"), #68b738;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 25px 30px;
}

.advantages__item--month::before {
  background: url("../img/stack.svg#clock"), #68b738;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 26px 31px;
}

@media (min-width: 768px) {
  .advantages__item::after {
    position: absolute;
    content: counter(item);
    font-size: 280px;
    color: #f2f2f2;
    font-family: "Oswald", "Arial", sans-serif;
    line-height: 100%;
    z-index: -1;
  }
}

@media (min-width: 768px) {
  .advantages__item--healthy::after {
    top: -10px;
    right: 26px;
  }
}

@media (min-width: 768px) {
  .advantages__item--powder::after {
    top: -10px;
    right: 6px;
  }
}

@media (min-width: 768px) {
  .advantages__item--meal::after {
    top: -10px;
    right: 4px;
  }
}

@media (min-width: 768px) {
  .advantages__item--month::after {
    top: -10px;
    right: 2px;
  }
}

.advantages__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  counter-reset: item 0;
}
@media (min-width: 768px) {
  .advantages__list {
    flex-direction: row;
    flex-wrap: wrap;
    column-gap: 134px;
    row-gap: 20px;
  }
}
@media (min-width: 1440px) {
  .advantages__list {
    gap: 73px;
  }
}

.example {
  position: relative;
  background-color: #eaeaea;
  padding: 16px 21px 35px;
